import React from "react"
import { useState ,useEffect} from "react"
import axios from "axios"
const Catg = ({ setBrandFilter, brandItems}) => {
  const [bdata,setbdata] = useState([])
  useEffect(() => {
    axios(
     {url: '/branddata',})
     .then(response => {
       setbdata(response.data)
       })
 }, [])
  
  
  
  
  const offBrandClick = ()=>{
    setBrandFilter(null)
  }
  return (
    <>
      <div className='category'>
        <div className='chead d_flex'>
          <h1 >Brands </h1>
        </div>
        
        {bdata.map((value, index) => {
          const onBrandClick = () => {
            const {brand} = value
            console.log(brand)
            setBrandFilter(brand)
          }
          return (
            <div className='box f_flex' key={index} onClick={onBrandClick}>
              <img className="img" src={value.Img} alt='' />
              <span>{value.brand}</span>
            </div>
          )
        })}
        
        <div className='box box2'>
          <button onClick={offBrandClick}>View All Brands</button>
        </div>
      </div>
    </>
  )
}

export default Catg
